import React, { useState } from "react";
import Overlay from "../../../../Search/Overlay";
import SearchModalWrapper from "../../../../Search/SearchModalWrapper";
import {
  useAllDepartment,
  useInstituteUnPinDepartment,
  useOneDepartmenteDelete,
} from "../../../Institute/Components/DepartmentApi/depart-api";
import style from "./Departments.module.css";
import PinDepartment from "../../../../optimized/InstituteMember/InstituteDepartment/PinDepartment/PinDepartment";
import QvipleLoader from "../../../../optimized/Loader/QvipleLoader";
import { useNavigate } from "react-router-dom";
import NotifyEvent from "../../../../optimized/Utils/Snackbar/NotifyEvent";
const EditDepartment = ({
  onId,
  onOpenMenu,
  editOneDepartment,
  onReftech,
  instituteId,
  department,
}) => {
  const selectoptions = ["Edit", "Delete"];
  const [step, setStep] = useState("");
  const [disabled, setDisabled] = useState(false);
  const navigate = useNavigate();
  const [departmentDelete] = useOneDepartmenteDelete();
  const [instituteUnPinDepartment] = useInstituteUnPinDepartment();
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  const selectBatchHandler = (option) => {
    if (option === "Edit") {
      editOneDepartment(onId);
      onOpenMenu();
    } else if (option === "Delete") {
      setDisabled((pre) => !pre);
      departmentDelete({
        did: onId,
      })
        .then((res) => {
          if (res?.data?.error) {
            setSnackbar({
              msg: res?.data?.message,
              open: true,
              variant: "error",
            });
            setDisabled((pre) => !pre);
          } else {
            setDisabled((pre) => !pre);
            navigate(-1);
            onOpenMenu();
          }
        })
        .catch({});
    }
    //  else if (option === "Pin Department") {
    // }
    else {
    }
  };

  const unpinned = () => {
    if (instituteId) {
      setDisabled((pre) => !pre);
      instituteUnPinDepartment({
        id: instituteId,
        bodyParameter: {
          did:
            department?.pin_status_flow === "DEPENDENT"
              ? department?.pin_status_id
              : department?._id,
          flow: department?.pin_status_flow,
        },
      })
        .then(() => {
          onReftech();
          setDisabled((pre) => !pre);
          onOpenMenu();
        })
        .catch({});
    }
  };

  return (
    <>
      {step === 1 ? (
        <PinDepartment
          onClose={() => setStep("")}
          onParentClose={onOpenMenu}
          instituteId={instituteId}
          departmentId={onId}
          onReftech={onReftech}
        />
      ) : (
        <>
          <SearchModalWrapper
            onClose={onOpenMenu}
            customStyleBackdrop={{ backgroundColor: "rgba(0, 0, 0, 0.5)" }}
          />
          <Overlay>
            <div
              className={style.edit_container}
              style={{
                width: "240px",
              }}
            >
              {department?.pin_status === "Pinned" ? (
                <h2 onClick={unpinned}>UnPin Department</h2>
              ) : (
                <h2 onClick={() => setStep(1)}>Pin Department</h2>
              )}
              {selectoptions.map((option, index) => (
                <h2
                  value={option}
                  key={index}
                  onClick={() => selectBatchHandler(option)}
                >
                  {option}
                </h2>
              ))}
            </div>
            {disabled && <QvipleLoader />}
          </Overlay>
        </>
      )}
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default EditDepartment;
