import { useTranslation } from "react-i18next";
import style from "../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { useInstitutePinDepartment } from "../../../../Department/Institute/Components/DepartmentApi/depart-api";
import { useState } from "react";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../Ui/Tab/Tile/TileCard";
import FormInput from "../../../Ui/Input/FormInput";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
const PinDepartment = ({
  onClose,
  instituteId,
  departmentId,
  onParentClose,
  onReftech,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    type: "",
    flow: "",
  });
  const [institutePinDepartment] = useInstitutePinDepartment();

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTile = (val) => {
    setState((prev) => ({
      ...prev,
      flow: val,
    }));
  };
  const onAddHeadPerson = () => {
    let validation = requiredField(state, ["type"]);
    if (existingRequiredField(validation) && departmentId && instituteId) {
      setDisabled((pre) => !pre);
      institutePinDepartment({
        id: instituteId,
        bodyParameter: {
          ...state,
          did: departmentId,
        },
      })
        .then(() => {
          onReftech();
          setDisabled((pre) => !pre);
          onParentClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  // console.info("pid", state);
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("pin_department")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <TileWrapper label="dependency">
              {list_depend?.map((tile) => (
                <TileCard
                  key={tile?.key}
                  tile={tile}
                  onTile={onTile}
                  activeTile={state?.flow}
                />
              ))}
            </TileWrapper>
            {state.flow === "DEPENDENT" ? (
              <FormInput
                label={t("section_name")}
                placeholder={t("section_name_placeholder")}
                name={"type"}
                value={state.type}
                type={"text"}
                onChange={onChange}
              />
            ) : null}

            <CreateButton label="save" onAction={onAddHeadPerson} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default PinDepartment;

const list_depend = [
  {
    key: "DEPENDENT",
    name: "dependent",
  },
  {
    key: "INDEPENDENT",
    name: "independent",
  },
];
