import style from "../../UiTab.module.css";
import { useTranslation } from "react-i18next";
const TileWrapper = ({
  label,
  customTileLabel,
  customTileContainer,
  customTileStyle,
  errorShow,
  children,
}) => {
  const { t } = useTranslation();
  return (
    <div className={style.tile_container} style={customTileStyle}>
      {label && (
        <h6 className={style.tile_label} style={customTileLabel}>
          {t(label)}

          {errorShow ? (
            <span
              style={{
                color: "rgb(231, 75, 75)",
              }}
            >
              * {t("form_require_label")}
            </span>
          ) : null}
        </h6>
      )}
      <section
        className={style.tile_tile_container}
        style={customTileContainer}
      >
        {children}
      </section>
    </div>
  );
};

export default TileWrapper;
